<template>
  <div class="Loser">
    <div class="orderInfo">
      <div class="ico icon">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          class="icon"
          viewBox="0 0 1024 1024"
          width="48"
          height="48"
          t="1613901118205"
          p-id="3010"
          version="1.1"
        >
          <path
            class="selected"
            fill="#ff5000"
            d="M 512 512 m -512 0 a 512 512 0 1 0 1024 0 a 512 512 0 1 0 -1024 0 Z"
            p-id="3011"
            data-spm-anchor-id="a313x.7781069.0.i0"
          />
          <path
            fill="#ffffff"
            d="M 315.822 666.317 a 29.6141 29.6141 0 0 0 41.8816 41.8611 l 154.255 -154.337 l 154.337 154.337 a 29.6141 29.6141 0 1 0 41.8816 -41.8611 l -154.337 -154.337 l 154.337 -154.296 a 29.6141 29.6141 0 1 0 -41.8816 -41.8611 l -154.337 154.337 l -154.255 -154.337 a 29.6141 29.6141 0 0 0 -41.8816 41.8611 l 154.317 154.296 Z m 0 0"
            p-id="3012"
          />
        </svg>
      </div>
      <p class="text">订单支付失败</p>
      <van-cell-group :border="false">
        <van-cell title="订单编号" :value="detail.order_id" :border="false" />
        <van-cell title="下单时间" :value="detail._add_time" :border="false" />
        <van-cell title="支付方式" :value="payType" :border="false" />
        <van-cell title="支付金额" :value="'￥'+detail.pay_price" :border="false" />
      </van-cell-group>
      <div class="buttom">
        <div class="look" @click="goDerail">查看订单</div>
        <div class="back" @click="goHome">返回首页</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Loser",
  data() {
    return {
      key: "",
      addressId: 0,
      detail:{},
      orderId:'',
      payType:''
    };
  },
  created() {
    this.key = this.$route.params.key;
    this.addressId = this.$route.params.id;

    this.getOrderId();
  },
  methods: {
    //获取订单id
    getOrderId() {
      let tokenString = this.$cookies.get("tokenString");

      this.axios({
        method: "POST",
        url: this.baseUrl + "/order/create/" + this.key,
        data: {
          addressId:this.addressId,
          bargainId: 0,
          combinationId: 0,
          couponId: 0,
          from: "weixinh5",
          mark: "",
          payType: "yue",
          phone: "",
          pinkId: 0,
          real_name: "",
          seckill_id: 0,
          shipping_type: 1,
          store_id: 0,
          useIntegral: 0,
        },
        headers: {
          "Authori-zation": "Bearer " + tokenString,
        },
      })
        .then((res) => {
          this.$toast({
            message: res.data.msg,
            forbidClick: true,
            duration: 3000,
          });
          if (res.data.status == 200) {
            this.orderId = res.data.data.result.orderId
            console.log(this.orderId);
            this.getorderDetail()
          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.log("err ==> ", err);
        });
    },

    //获取订单详情
    getorderDetail(){
      let tokenString = this.$cookies.get("tokenString");

      this.axios({
        method: "GET",
        url: this.baseUrl + "/order/detail/" + this.orderId,
        headers: {
          "Authori-zation": "Bearer " + tokenString,
        },
      })
        .then((res) => {
          if (res.data.status == 200) {
            this.detail = res.data.data;
            this.payType = this.detail._status._payType

          }
        })
        .catch((err) => {
          this.$toast.clear();
          console.log("err ==> ", err);
        });
    },

    //返回首页
    goHome(){
      this.$router.push("/")
    },

    //查看订单详情
    goDerail(){
      this.$router.push({name:"OrderDetail" , params:{orderId : this.orderId}})
    }

  },
};
</script>

<style lang="less" scoped>
.Loser {
  background: #ff8600;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.orderInfo {
  width: 90%;
  background: #fff;
  border-radius: 10px;
  position: relative;
  padding: 20px;
}
.ico {
  position: absolute;
  top: -24px;
  left: 50%;
  transform: translateX(-50%);
}
.icon {
  width: 58px;
  height: 58px;
}
.text {
  text-align: center;
  margin-top: 30px;
  font-size: 20px;
  font-weight: bold;
}
.van-cell-group {
  margin: 10px 0;
  padding: 10px 0;
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
}
.buttom {
  div {
    padding: 10px 0;
    text-align: center;
    font-size: 16px;
    border-radius: 99px;
    border: 1px solid #ff5000;
  }
  .look {
    background: #ff5000;
    margin-bottom: 10px;
    color: #fff;
  }
  .back {
    color: #ff5000;
  }
}
.van-cell {
  .van-cell__title{
    flex: 1;
  }
  .van-cell__value{
    flex: 2;
  }
}
</style>